import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from '../../knowledgeBlocks.scss';

const view = (state) => {
	const {
		properties: {
			blocks,
			parentSysId
		}
	} = state;


	let knowledge_block_items = blocks.map((block) =>
		<li>
			<sn-knowledge-blocks-item
				sysId={block.sys_id}
				number={block.number}
				description={block.short_description}
				blockVersion={block.version}
				updatedOn={block.updated_on}
				canRead={block.can_read}
				cannotRead={block.cannot_read}
				parentSysId={parentSysId}
			/>
		</li>
	);

	knowledge_block_items = knowledge_block_items
	? <ul className='block-list-item'>{knowledge_block_items}</ul>
	: knowledge_block_items

	return (
		<section className='block-card-list'>
			{knowledge_block_items}
		</section>
	);
};

createCustomElement('sn-knowledge-blocks-results-list', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		blocks: [],
		parentSysId: ''
	}
});
