import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import nomatch from './icons/no-result-found.svg';
import {t} from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-text-link';
import {NEW_BLOCK_CREATE_STARTED} from './constants';


const KbNotFoundImageWithMessage = ({ message = '', subMessage = '' }) => {
	return (
		<div
			className='block-panel-body-container block-center'
			aria-label={t(message)}
		>
			<div className='block-content-section'>
				<section className="block-empty-state">
					<div className="block-empty-state-row">
						<img
							src={nomatch}
							alt={ message }
						/>
					</div>
					<h3 className="block-empty-state-row">
						{ message }
					</h3>
					{subMessage && (
						<div>
							<strong className="block-empty-state-row sub-message">
								{ subMessage }
							</strong>
						</div>
					)}
				</section>
			</div>
		</div>
	);
};

/** 
 * @seismicView sn-knowledge-blocks-connected
*/
export const view = (state, {dispatch}) => {
	const {
		properties: {
			table,
			sysId,
			fields
		}
	} = state;
	const knowledge_base = fields && fields.kb_knowledge_base && fields.kb_knowledge_base.value;
	const article_language = fields && fields.language ? fields.language.value : 'en';
	const knowledge_id = fields && fields.sys_id;

	if (!knowledge_base) {
		return (
			<Fragment>
				<div className='block-panel'>
					<sn-knowledge-blocks-headerbar/>
					<div className='block-panel-body-container'>
						<div className='block-content-section'>
							<KbNotFoundImageWithMessage message={t("Select a knowledge base to add blocks")}/>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}

	if (!state.isBlocksEnabled) {
		return (
			<Fragment>
				<div className='block-panel'>
					<sn-knowledge-blocks-headerbar/>
					<div className='block-panel-body-container'>
						<div className='block-content-section'>
							<KbNotFoundImageWithMessage
								message={t("Blocks are not enabled")}
								subMessage={t("Enable knowledge blocks for the selected knowledge base.")}
							/>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}

	if (state.defaultBlocks && state.defaultBlocks.length === 0 && state.showDefaultBlocks) {
		return (
			<Fragment>
				<div className='block-panel'>
					<sn-knowledge-blocks-headerbar/>
					<div className='block-panel-body-container'>
						<div className='block-content-section'>
							<KbNotFoundImageWithMessage
								message={t("No blocks available")}
								subMessage={t("Create knowledge blocks in the selected knowledge base.")}
							/>
							<div className='block-button-container'>
								<now-button
									label={t("Create Block")}
									size='md'
									variant='primary'
									className='block-button-control button-center'
									on-click={(e) => dispatch(NEW_BLOCK_CREATE_STARTED, {})}
								/>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}

	const knowledge_blocks = state.blocks.length > 0
		? (<div className='block-results-container'>
			<div tabIndex="-1">
				<sn-knowledge-blocks-results-list blocks={state.blocks} parentSysId={knowledge_id}/>
			</div>
		</div>) : (<div className='block-panel-body-container'>
			<section className='block-empty-state'>
				<KbNotFoundImageWithMessage
					message="No results Found"
					subMessage="Refine your search."
				/>
			</section>
		</div>);

	const knowledge_blocks_search = !state.showAdvancedSearch ?
		(<Fragment>
			<sn-knowledge-blocks-search-box
				parentId={knowledge_id}
				knowledgeBase={knowledge_base}
				articleLanguage={article_language}
				inputValue={state.inputValue}
			/>
			{knowledge_blocks}
		</Fragment>) : '';

	const pagination_section = !state.showAdvancedSearch && !state.showAdvancedSearch && state.blocks.length > 0
		? <sn-knowledge-blocks-pagination showPrev={state.showPrev} showNext={state.showNext}/> : '';

	let knowledge_blocks_main = !state.showContent && !state.showAdvancedSearch ? (
		<div className='block-panel'>
			<sn-knowledge-blocks-headerbar/>
			<div className='block-panel-body-container'>
				{ knowledge_blocks_search }
				{ pagination_section }
			</div>
		</div>
	) : '';

	if (state.showContent) {
		knowledge_blocks_main = (<sn-knowledge-blocks-content parentId={knowledge_id} blockDetails={state.selectedBlock}
															 blockIds={state.blockIds}/>);
	} else if (state.showAdvancedSearch) {
		knowledge_blocks_main = (<sn-knowledge-blocks-condition-builder show={state.showAdvancedSearch} encodedQuery={state.encodedQuery}/>);
	}

	return (
		<Fragment>
			{knowledge_blocks_main}
		</Fragment>
	);
};
