import {
	BLOCK_SEARCH_STARTED,
	BLOCK_FETCH_STARTED,
	BLOCK_FETCH_SUCCEEDED,
	BLOCK_IDS_FETCH_STARTED,
	BLOCK_IDS_FETCH_SUCCEEDED,
	SNIPPET_INSERT_STARTED,
	INSERT_SNIPPET_FETCH_STARTED,
	INSERT_SNIPPET_FETCH_SUCCEEDED,
	FETCH_FAILED,
	PREVIEW_RECORD,
	BLOCK_CONTENT_HANDLE_STARTED,
	BLOCK_CONTENT_FETCH_STARTED,
	BLOCK_CONTENT_FETCH_SUCCEEDED,
	MAIN_DETAILS_FETCH_STARTED,
	ADVANCED_SEARCH_FETCH_STARTED,
	ADVANCED_SEARCH_CLOSED,
	BLOCKS_WITH_QUERY_FETCH_STARTED,
	NEW_BLOCK_CREATE_STARTED,
	BLOCK_PAGE_FETCH_STARTED,
	MAX_ROW_COUNT,
	BLOCK_ENABLED_CHECK_STARTED,
	CHECK_IS_BLOCK_ENABLED_SUCCEEDED,
	CURRENT_SEARCH_TEXT_ASSIGNED,
} from './constants';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED} = actionTypes;
import {createGraphQLEffect} from "@servicenow/ui-effect-graphql";
import search_data_read from "./graphql/searchQuery";
import block_ids_data_read from "./graphql/blockIdsQuery";
import insert_snippet_data_read from "./graphql/blocksSnippetQuery";
import display_content_data_read from "./graphql/displayContentQuery";
import check_is_blocks_enabled from "./graphql/checkBlocksEnabled";


const fetchKnowledgeBlocksEffect = createGraphQLEffect(search_data_read, {
	successActionType: BLOCK_FETCH_SUCCEEDED,
	variableList: ['knowledgeBase','articleLanguage', 'startIndex', 'rowCount', 'searchText', 'searchQuery']
});

const fetchKnowledgeBlockIdsEffect = createGraphQLEffect(block_ids_data_read, {
	successActionType: BLOCK_IDS_FETCH_SUCCEEDED,
	variableList: ['knowledgeBase','articleLanguage', 'searchText', 'searchQuery']
});

const fetchInsertSnippetEffect = createGraphQLEffect(insert_snippet_data_read, {
	successActionType: INSERT_SNIPPET_FETCH_SUCCEEDED,
	variableList: ['knowledgeBlockIds']
});

const fetchBlockContentEffect = createGraphQLEffect(display_content_data_read, {
	successActionType: BLOCK_CONTENT_FETCH_SUCCEEDED,
	variableList: ['knowledgeBlockId']
});

const fetchIsBlocksEnabledEffect = createGraphQLEffect(check_is_blocks_enabled, {
	successActionType: CHECK_IS_BLOCK_ENABLED_SUCCEEDED,
	variableList: ['kbKnowledgeBaseSysId']
});

const handleComponentConnectedEffect = {
	effect(props) {
		let kbKnowledgeBaseSysId = props.properties.fields.kb_knowledge_base.value;
		let language = props.properties.fields.language ? props.properties.fields.language.value : 'en';
		if (kbKnowledgeBaseSysId) {
			props.dispatch(BLOCK_ENABLED_CHECK_STARTED, { kbKnowledgeBaseSysId });
			props.dispatch(BLOCK_FETCH_STARTED, {
				knowledgeBase: kbKnowledgeBaseSysId,
				articleLanguage: language,
				startIndex: 0,
				rowCount: MAX_ROW_COUNT,
				searchText: ''
			});
			props.dispatch(BLOCK_IDS_FETCH_STARTED, {
				knowledgeBase: kbKnowledgeBaseSysId,
				articleLanguage: language,
				searchText: ''
			});
		}
	}
};

const handleCheckIsBlockEnabledSucceeded = ({action, state, updateState}) => {
	let isBlocksEnabled = action.payload.data.AppKnowledgeBlocks_Query.isBlocksEnabled;

	if (isBlocksEnabled) {
		isBlocksEnabled = JSON.parse(isBlocksEnabled);
		updateState({ isBlocksEnabled });
	}
};

const handleKnowledgeBlocksFetchSucceeded = ({action, state, updateState}) => {
	let blocks = action.payload.data.AppKnowledgeBlocks_Query.getKnowledgeBlocks;

	if (blocks) {
		blocks = JSON.parse(blocks);
		let newStateDelta = {
			blocks: blocks.result,
			showNext: blocks.moreResults,
			showPrev: state.startIndex > 0
		};

		if (state.defaultBlocks && state.defaultBlocks.length == 0) {
			newStateDelta = Object.assign(newStateDelta, {
				defaultBlocks: blocks.result
			});
		}
		updateState(newStateDelta);
	}
};

const handleKnowledgeBlockIdsFetchSucceeded = ({action, state, updateState}) => {
	let blockIds = action.payload.data.AppKnowledgeBlocks_Query.getKnowledgeBlockIds;

	if (blockIds) {
		blockIds = JSON.parse(blockIds);
		updateState({
			blockIds: blockIds
		});
	}
};

const handleInsertSnippetFetchSucceeded = ({action}) => {
	const result = JSON.parse(action.payload.data.AppKnowledgeBlocks_Query.getKnowledgeBlocksSnippet);
	const editor = tinyMCE.activeEditor;

	if (editor && result && result.length > 0) {
		for (let i=0; i<result.length; i++) {
			let idBlock = result[i].idBlock;
			let blockdetails = result[i].blockdetails;
			let snippet = {
				id: idBlock,
				payload: blockdetails
			}
			editor.execCommand('snInsertContentAndScroll', false, snippet);
		}
	}
};

const handleBlockContentFetchSucceeded = ({action, updateState}) => {
	const selectedBlock = JSON.parse(action.payload.data.AppKnowledgeBlocks_Query.getKnowledgeBlockDisplayContent);
	updateState({
		selectedBlock: selectedBlock,
		showContent: true
	});
};

const handleFetchFailed = ({action}) => console.error('Fetch failed!');

const searchBlocks = (coeffects) => {
	const {action, dispatch, state, updateState} = coeffects;
	action.stopPropagation();
	updateState({
		inputValue: action.payload.searchText,
		showDefaultBlocks: !action.payload.searchText
	});
	dispatch(BLOCK_FETCH_STARTED, {
		knowledgeBase: action.payload.knowledgeBase,
		articleLanguage: action.payload.articleLanguage,
		startIndex: state.startIndex,
		rowCount: MAX_ROW_COUNT,
		searchText: action.payload.searchText
	});

	if (action.payload.clearIds) {
		dispatch(BLOCK_IDS_FETCH_STARTED, {
			knowledgeBase: action.payload.knowledgeBase,
			articleLanguage: action.payload.articleLanguage,
			searchText: action.payload.searchText
		});
	}
};

const searchBlocksWithQuery = ({action, dispatch, state, updateState, properties}) => {
	action.stopPropagation();
	let { encodedQuery } = action.payload;
	let fields = properties.fields;

	updateState({
		encodedQuery
	});
	dispatch(BLOCK_FETCH_STARTED, {
		knowledgeBase: fields.kb_knowledge_base ? fields.kb_knowledge_base.value : 'dfc19531bf2021003f07e2c1ac0739ab',
		articleLanguage: fields.language ? fields.language.value : 'en',
		startIndex: state.startIndex,
		rowCount: MAX_ROW_COUNT,
		searchText: state.inputValue,
		searchQuery: encodedQuery
	});

	if (action.payload.clearIds) {
		dispatch(BLOCK_IDS_FETCH_STARTED, {
			knowledgeBase: fields.kb_knowledge_base ? fields.kb_knowledge_base.value : 'dfc19531bf2021003f07e2c1ac0739ab',
			articleLanguage: fields.language ? fields.language.value : 'en',
			searchText: state.inputValue,
			searchQuery: encodedQuery
		});
	}

};

const handleBlockPagination = (coeffects) => {
	const {action, state, properties, updateState, dispatch} = coeffects;
	action.stopPropagation();

	if (action.payload.type === 'NEXT') {
		updateState({
			startIndex: state.startIndex + MAX_ROW_COUNT
		});
	} else {
		updateState({
			startIndex: state.startIndex - MAX_ROW_COUNT
		});
	}

	let fields = properties.fields;
	dispatch(BLOCK_SEARCH_STARTED, {
		knowledgeBase: fields.kb_knowledge_base ? fields.kb_knowledge_base.value : 'dfc19531bf2021003f07e2c1ac0739ab',
		articleLanguage: fields.language ? fields.language.value : 'en',
		searchText: state.inputValue,
		clearIds: false
	});
};

const handleAdvancedSearch = (coeffects) => {
	const {action, updateState, dispatch} = coeffects;
	action.stopPropagation();
	const showAdvancedSearch = action.payload.showAdvancedSearch;
	updateState({
		showAdvancedSearch: showAdvancedSearch
	});
};

const handleCloseAdvancedSearch = (coeffects) => {
	const {action, dispatch} = coeffects;
	action.stopPropagation();
	dispatch(ADVANCED_SEARCH_FETCH_STARTED, {
		showAdvancedSearch: false
	})
};

const handleInsertSnippet = (coeffects) => {
	const {action, dispatch} = coeffects;

	action.stopPropagation();
	dispatch(INSERT_SNIPPET_FETCH_STARTED, {
		knowledgeBlockIds: action.payload.selectedBlockId
	});
};

const handleBlockContent = (coeffects) => {
	const {action, updateState, dispatch} = coeffects;
	action.stopPropagation();
	const selectedBlockId = action.payload.selectedBlockId;
	updateState({
		selectedBlockId: selectedBlockId
	});
	dispatch(BLOCK_CONTENT_FETCH_STARTED, {
		knowledgeBlockId: selectedBlockId
	});
};

const handleShowMainDetails = (coeffects) => {
	const {action, dispatch, updateState} = coeffects;
	action.stopPropagation();

	updateState({
		showContent: action.payload.showContent
	});
};

const handleCreateNewBlock = (coeffects) => {
	const {action, dispatch} = coeffects;

	action.stopPropagation();

	let payload = {
		table: 'kb_knowledge_block',
		sys_id: -1,
		params: {
			sysparm_parent_table: 'kb_knowledge',
			sysparm_parent_sys_id: action.payload.knowledgeId,
			sysparm_kb_knowledge_base: action.payload.knowledgeBase
		}
	};

	dispatch(PREVIEW_RECORD, payload);
};

const handleSetCurrentSearchText = (coeffects) => {
	const {action, dispatch, updateState} = coeffects;

	action.stopPropagation();

	let { searchText } = action.payload;
	updateState({
		inputValue: searchText
	});
};

export const actions = {
	[BLOCK_SEARCH_STARTED]: {
		private: true
	},
	[BLOCK_FETCH_STARTED]: {
		private: true
	},
	[BLOCK_FETCH_SUCCEEDED]: {
		private: true
	},
	[SNIPPET_INSERT_STARTED]: {
		private: true
	},
	[INSERT_SNIPPET_FETCH_STARTED]: {
		private: true
	},
	[INSERT_SNIPPET_FETCH_SUCCEEDED]: {
		private: true
	},
	[BLOCK_CONTENT_HANDLE_STARTED]: {
		private: true
	},
	[BLOCK_CONTENT_FETCH_STARTED]: {
		private: true
	},
	[BLOCK_CONTENT_FETCH_SUCCEEDED]: {
		private: true
	},
	[FETCH_FAILED]: {
		private: true
	},
	[MAIN_DETAILS_FETCH_STARTED]: {
		private: true
	},
	[ADVANCED_SEARCH_FETCH_STARTED]: {
		private: true
	},
	[NEW_BLOCK_CREATE_STARTED]: {
		private: true
	},
	[BLOCK_PAGE_FETCH_STARTED]: {
		private: true
	},
	[BLOCKS_WITH_QUERY_FETCH_STARTED]: {
		private: true
	}
};

export const actionHandlers = {
	[BLOCK_SEARCH_STARTED]: searchBlocks,
	[BLOCKS_WITH_QUERY_FETCH_STARTED]: searchBlocksWithQuery,
	[BLOCK_FETCH_STARTED]: fetchKnowledgeBlocksEffect,
	[BLOCK_FETCH_SUCCEEDED] : handleKnowledgeBlocksFetchSucceeded,
	[BLOCK_IDS_FETCH_STARTED]: fetchKnowledgeBlockIdsEffect,
	[BLOCK_IDS_FETCH_SUCCEEDED] : handleKnowledgeBlockIdsFetchSucceeded,
	[SNIPPET_INSERT_STARTED] : handleInsertSnippet,
	[INSERT_SNIPPET_FETCH_STARTED]: fetchInsertSnippetEffect,
	[INSERT_SNIPPET_FETCH_SUCCEEDED] : handleInsertSnippetFetchSucceeded,
	[BLOCK_CONTENT_HANDLE_STARTED] : handleBlockContent,
	[BLOCK_CONTENT_FETCH_STARTED]: fetchBlockContentEffect,
	[BLOCK_CONTENT_FETCH_SUCCEEDED] : handleBlockContentFetchSucceeded,
	[FETCH_FAILED] : handleFetchFailed,
	[MAIN_DETAILS_FETCH_STARTED]: handleShowMainDetails,
	[ADVANCED_SEARCH_FETCH_STARTED]: handleAdvancedSearch,
	[ADVANCED_SEARCH_CLOSED]: handleCloseAdvancedSearch,
	[NEW_BLOCK_CREATE_STARTED]: handleCreateNewBlock,
	[BLOCK_PAGE_FETCH_STARTED]: handleBlockPagination,
	[BLOCK_ENABLED_CHECK_STARTED]: fetchIsBlocksEnabledEffect,
	[CHECK_IS_BLOCK_ENABLED_SUCCEEDED]: handleCheckIsBlockEnabledSucceeded,
	[CURRENT_SEARCH_TEXT_ASSIGNED]: handleSetCurrentSearchText,
	[COMPONENT_CONNECTED]: handleComponentConnectedEffect,
	[COMPONENT_PROPERTY_CHANGED]: handleComponentConnectedEffect
};
