import { createCustomElement } from '@servicenow/ui-core';
import { actionMappers } from './actionMappers';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';

/**
 * Knowledge Blocks is a connected component that will be used in the contextual sidebar for a Knowledge form.
 * It can be used to search for blocks and insert them into an article text.
 * It can also be used to create new blocks and view existing blocks.
 *
 * ```jsx
 * <sn-knowledge-blocks-connected-uib 
		table={table}
		sysId={sysId}
		fields={fields}
	></sn-knowledge-blocks-connected-uib>
 * ```
 *
 * @seismicElement sn-knowledge-blocks-connected-uib
 * @summary A connected component for searching and inserting knowledge blocks in an article."
 * @uib.label Knowledge blocks
 * @uib.icon tile-uib-knowledge-blocks-1-sm
 * @uib.description Connected component for adding knowledge blocks in an article
 * @uib.category component
 * @uib.associatedTypes global.record-aware
 * @uib.properties table, sysId, fields
 */
export default createCustomElement('sn-knowledge-blocks-connected-uib', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {},
	properties: {
		/**
		 * The parent table
		 * @type {string}
		 * @uib.label Table
		 * @uib.description The parent table
		*/
		table: { default: '' },
		/**
		 * The system ID of the article into which knowledge blocks are being added
		 * @type {string}
		 * @uib.label Sys ID
		 * @uib.description The system ID of the article into which knowledge blocks are being added
		*/
		sysId: { default: '' },
		/**
		 * The field names and values fetched from the parent record
		 * @type {json}
		 * @uib.label Fields
		 * @uib.description The field names and values fetched from the parent record
		*/
		fields: { default: {} }
	},
	actionHandlers: actionMappers,
	dispatches: {
	/**
	 * Dispatched when a block link is clicked or when the new button is clicked
	 * @type {object}
	*/
		'SN_KNOWLEDGE_BLOCKS_CONNECTED#OPEN_RECORD_REQUESTED': { schema: { type: 'object' } },
	}
});