import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from '../../knowledgeBlocks.scss';
import {BLOCK_PAGE_FETCH_STARTED} from "../../constants";

const handlePrevPage = (event, state, dispatch) => {
	event.stopPropagation();
	if (event.target.disabled) {
        return;
    }
	dispatch(BLOCK_PAGE_FETCH_STARTED, {
		type: 'PREV'
	});
};

const handleNextPage = (event, state, dispatch) => {
	event.stopPropagation();
	if (event.target.disabled) {
        return;
    }
	dispatch(BLOCK_PAGE_FETCH_STARTED, {
		type: 'NEXT'
	});
};

const view = (state, {dispatch}) => {
	const { showNext, showPrev } = state.properties;

	return (
		<footer className='block-footer'>
			<div className='block-footer-pagination-container'>
				<now-button
					bare
					icon='arrow-left-outline'
					disabled={!showPrev ? true : false}
					size="md"
					variant="tertiary"
					config-aria={{
						button: {'aria-label': t('Previous Page')}
					}}
					on-click={(e) => handlePrevPage(e, state, dispatch)}/>
				<now-button
					bare
					icon='arrow-right-outline'
					disabled={!showNext ? true : false}
					size="md"
					variant="tertiary"
					config-aria={{
						button: {'aria-label': t('Next Page')}
					}}
					on-click={(e) => handleNextPage(e, state, dispatch)}/>
			</div>
		</footer>
	)
};

createCustomElement('sn-knowledge-blocks-pagination', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		showPrev: true,
		showNext: true
	}
});
