import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {SnTimeAgo} from 'sn-component-timeago';

import {view} from './view.js';

createCustomElement('sn-blocks-time-ago',{
	renderer : {
		type: snabbdom,
		view,
		transformState(state) {
			return {
				...state,
				time: state.properties.lastUpdated.getTickTime().time
			};
		}
	},
	properties : {
		timeStamp : {default : ''},
		systemTime : {default : ''},
		behaviors : {default : ''},
		lastUpdated : {
			computed(state) {
				const {
					timeStamp,
					lastUpdated,
					systemTime,
					// behaviors
				} = state.properties;
				if (lastUpdated) {
					lastUpdated.remove();
				}

				const options = {
					timeAgo: true,
					dateBoth: false,
					systemTime: systemTime,
					dateOnly: false
				};
				return new SnTimeAgo(state.properties, timeStamp, options);
			}
		}
	}
});