const block_ids_data_read = `query($knowledgeBase:String!, $articleLanguage:String!, $searchText:String, $searchQuery:String){
  AppKnowledgeBlocks_Query {
    getKnowledgeBlockIds(knowledgeBase: $knowledgeBase, articleLanguage:$articleLanguage, searchText:$searchText,
    	searchQuery:$searchQuery)
  }
}`;

export default block_ids_data_read;
