import '@servicenow/now-button';
import '@servicenow/now-rich-text';
import '@servicenow/now-icon';
import {t} from "sn-translate";
import { getActiveClass, handleInsertBlock, handleHover } from "./actions";
import {
	BLOCK_CONTENT_FETCHED,
	NEXT_BLOCK,
	PREV_BLOCK
} from './constants';

import {
	MAIN_DETAILS_FETCH_STARTED
} from '../../constants';

export const view = (state, {updateState, dispatch}) => {
	const {
		properties: {
			blockDetails
		}
	} = state;

	const carouselPrevBtn = (
		<now-button bare variant="tertiary" icon-name="chevron-left-fill" className={getActiveClass(PREV_BLOCK, state, updateState)}
					on-click={(e)=>{
						e.stopPropagation();
						if (state.showPrev)
							dispatch(BLOCK_CONTENT_FETCHED, {
								type: PREV_BLOCK
							});
					}}
					config-aria={{
						button: {'aria-label': t('Previous Block')}
					}}
					disabled={!state.showPrev ? true : false} aria-label={t('previous')}>
		</now-button>
	);
	const carouselNextBtn = (
		<now-button bare variant="tertiary" icon-name="chevron-right-fill" className={getActiveClass(NEXT_BLOCK, state, updateState)}
					on-click={(e)=>{
						e.stopPropagation();
						if (state.showNext)
							dispatch(BLOCK_CONTENT_FETCHED, {
								type: NEXT_BLOCK
							});
					}}
					config-aria={{
						button: {'aria-label': t('Next Block')}
					}}
					disabled={!state.showNext ? true : false} aria-label={t('next')}>
		</now-button>
	);

	return (
		<div className='block-content-container'>
			<div className='block-content-header-container'>
				<now-button type="Tertiary" icon-set="solid" icon-name="chevron-left-outline" bare autoFocus size="md"
							on-click={()=>dispatch(MAIN_DETAILS_FETCH_STARTED, {
								showContent: false
							})}
							config-aria={{
								button: {'aria-label': t('Previous Page')}
							}}/>
				<div className='block-content-action-container'>
					<now-button type="Tertiary" bare autoFocus size="md" label={t('Insert Block')}
								on-click={(e) => handleInsertBlock(e, dispatch, state.properties.blockDetails.sysId)}
								aria-label={t('Insert Block')} />
				</div>
			</div>
			<div className='block-content-section'>
				<div className='block-header-section'>
					<now-text-link label={blockDetails.number} variant="primary" href='javascript:void(0);'/>
					<div className='block-header kb-ellipsis'>{blockDetails.shortDesc}</div>
					<div className='block-item'>
						<now-icon icon="address-book-fill" size="md"></now-icon>
						<div className='block-subitem'>
							{blockDetails.author}
						</div>
						<br/>
						<now-icon icon="calendar-fill" size="md"></now-icon>
						<div className='block-subitem'>
							{blockDetails.updatedOn}
						</div>
					</div>
				</div>
				<div className='block-content'>
					<now-rich-text html={blockDetails.content}/>
				</div>
			</div>
			<div className="block-slider">
				{carouselPrevBtn}
				{carouselNextBtn}
			</div>
		</div>
	)
};
