import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-text-link';
import { t } from 'sn-translate';
import styles from '../../knowledgeBlocks.scss';
import {
	BLOCK_SEARCH_STARTED,
	ADVANCED_SEARCH_FETCH_STARTED,
	NEW_BLOCK_CREATE_STARTED,
	CURRENT_SEARCH_TEXT_ASSIGNED
} from '../../constants';

const TEXT_LINK_CLICKED = 'NOW_TEXT_LINK#CLICKED';

const handleCreateNewBlock = (event, state, dispatch, knowledgeId, knowledgeBase, articleLanguage) => {
	event.stopPropagation();

	let payload = {
		knowledgeId: knowledgeId,
		knowledgeBase: knowledgeBase,
		articleLanguage: articleLanguage
	};

	dispatch(NEW_BLOCK_CREATE_STARTED, payload);
};

const searchBlocks = (event, dispatch, knwoledgeBase, articleLanguage) => {
	event.stopPropagation();
	const text = event.target.value;

	dispatch(BLOCK_SEARCH_STARTED, {
		knowledgeBase: knwoledgeBase,
		articleLanguage: articleLanguage,
		searchText: event.target.value,
		clearIds: true
	});
	dispatch(CURRENT_SEARCH_TEXT_ASSIGNED, {
		searchText: event.target.value
	});
};

const handleAdvancedSearch = (coeffects) => {
	const {action, dispatch} = coeffects;

	action.stopPropagation();
	dispatch(ADVANCED_SEARCH_FETCH_STARTED, {
		showAdvancedSearch: true
	});
};

const view = (state, {dispatch}) => {
	const {
		properties: {
			parentId,
			knowledgeBase,
			articleLanguage
		}
	} = state;

	return (
		<div className='block-search-section'>
			<div className='block-search-control'>
				<div className='block-search-control-field'>
					<div className="block-search-control-icon">
						<now-icon icon="magnifying-glass-fill" size="md" />
					</div>
					<label className="hidden" for="search_blocks">{t('Search Blocks')}</label>
					<input type='text' value={state.properties.inputValue}
						   id="search_blocks"
						   on-input={e => searchBlocks(e, dispatch, knowledgeBase, articleLanguage)}
						   className='block-search-input' placeholder={t('Search Blocks')}
						   aria-label={t('Search Blocks')}/>
				</div>
				<now-button bare variant="tertiary" icon-name="plus-outline" size='lg'
							on-click={(e) => handleCreateNewBlock(e, state, dispatch, parentId, knowledgeBase, articleLanguage)}
							config-aria={{
								button: {'aria-label': t('Create New Block Search')}
							}}>
				</now-button>
			</div>
			<div className='block-advanced-search'>
				<now-text-link
					variant="primary" href='javascript:void(0);'
					label={t('Advanced Search')}
					aria-label={t('Advanced Search')}
				/>
			</div>
		</div>
	);
};

createCustomElement('sn-knowledge-blocks-search-box', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		parentId: '',
		knowledgeBase: '',
		articleLanguage: '',
		inputValue: ''
	},
	actionHandlers: {
		[TEXT_LINK_CLICKED]: handleAdvancedSearch
	}
});
