import { SnTimeAgo } from 'sn-component-timeago';//eslint-disable-line no-unused-vars

const update = (lastUpdated) => {
	return () => {
		lastUpdated.remove();
	};
};

export const view = (state) => {
	const {
		properties: {lastUpdated},
		time
	} = state;
	lastUpdated.setUpdateFn(update(lastUpdated));
	return (
		<time>
			{time}
		</time>
	);
    
};