const search_data_read = `query($knowledgeBase:String!, $articleLanguage:String!, $startIndex:Int!, $rowCount: Int!,
	$searchText:String, $searchQuery:String){
  AppKnowledgeBlocks_Query {
    getKnowledgeBlocks(knowledgeBase: $knowledgeBase, articleLanguage:$articleLanguage, startIndex:$startIndex,
    	rowCount:$rowCount, searchText:$searchText, searchQuery:$searchQuery)
  }
}`;

export default search_data_read;
