import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import './components/kbBlocksHeaderBar';
import './components/kbBlocksSearchBox';
import './components/kbBlocksNoResult';
import './components/kbBlocksResultsList';
import './components/kbBlocksItem';
import './components/KbBlocksContent';
import './components/kbBlocksPagination';
import './components/kbBlocksConditionBuilder';
import styles from './knowledgeBlocks.scss';
import {actions, actionHandlers} from './actions';
import {view} from './view';

/**
 * Knowledge Blocks is a connected component that will be used in the contextual sidebar for a Knowledge form.
 * It can be used to search for blocks and insert them into an article text.
 * It can also be used to create new blocks and view existing blocks.
 *
 * ```jsx
 * <sn-knowledge-blocks-connected 
		table={table}
		sysId={sysId}
		fields={fields}
	></sn-knowledge-blocks-connected>
 * ```
 *
 * @seismicElement sn-knowledge-blocks-connected
 * @summary A connected component for searching and inserting knowledge blocks in an article."
 */
createCustomElement('sn-knowledge-blocks-connected', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		isBlocksEnabled: false,
		blocks: [],
		blockIds: [],
		selectedBlock: {},
		inputValue: '',
		selectedBlockId: '',
		disableInsert: true,
		showAdvancedSearch: false,
		startIndex: 0,
		showContent: false,
		encodedQuery: 'active=true',
		showDefaultBlocks: true,
		defaultBlocks: []
	},
	actions,
	actionHandlers,
	properties: {
		/**
		 * The parent table
		 * @type {string}
		 * @uib.label Table
		 * @uib.description The parent table
		*/
		table: {
			default: ''
		},
		/**
		 * The system ID of the article into which knowledge blocks are being added
		 * @type {string}
		 * @uib.label Sys ID
		 * @uib.description The system ID of the article into which knowledge blocks are being added
		*/
		sysId: {
			default: ''
		},
		/**
		 * The field names and values fetched from the parent record
		 * @type {json}
		 * @uib.label Fields
		 * @uib.description The field names and values fetched from the parent record
		*/
		fields: {
			default: {}
		}
	}
});
