import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-condition-builder';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from '../../knowledgeBlocks.scss';
import {
	ADVANCED_SEARCH_FETCH_STARTED,
	ADVANCED_SEARCH_CLOSED,
	BLOCKS_WITH_QUERY_FETCH_STARTED
} from '../../constants';

const NOW_MODAL_CLOSED = 'NOW_MODAL#OPENED_SET';
const ENCODED_QUERY_CHANGED = 'NOW_CONDITION_BUILDER#ENCODED_QUERY_SET';
const ENCODED_QUERY_CHANGED_SUBMITTED = 'SN_KNOWLEDGE_BLOCKS_CONNECTED#ENCODED_QUERY_CHANGED_SUBMITTED';

const handleCloseModal = ({action, dispatch, properties}) => {
	action.stopPropagation();

	console.log(properties.encodedQuery);

	dispatch(ADVANCED_SEARCH_FETCH_STARTED, {
		showAdvancedSearch: false
	});
};

const handleEncodedQueryChangeSubmit = ({action, dispatch, state}) => {
	action.stopPropagation();
	console.log(action.payload.encodedQuery);
	dispatch(BLOCKS_WITH_QUERY_FETCH_STARTED, {
		encodedQuery: state.properties.encodedQuery
	});
	dispatch(ADVANCED_SEARCH_FETCH_STARTED, {
		showAdvancedSearch: false
	});
};

const handleEncodedQueryChange = ({action, updateProperties, properties}) => {
	action.stopPropagation();
	const encodedQuery = action.payload.value;
	if (encodedQuery!=properties.encodedQuery) {
		updateProperties({ encodedQuery });
	}
}

const view = (state, {dispatch}) => {
	let { encodedQuery, show } = state.properties;
	return (
		<now-modal
			footerActions={[
				{"label":t("Save"),"variant":"primary", 'clickActionType': ENCODED_QUERY_CHANGED_SUBMITTED},
				{"label":t("Cancel"),"variant":"secondary", 'clickActionType': ADVANCED_SEARCH_CLOSED}
			]}
			headerLabel={t("Advanced Search")}
			headerLevel="2"
			opened={show}
			size="lg">
			<now-condition-builder
				hideRelatedListQueryConditions='false'
				tableName="kb_knowledge_block"
				tableLabel="Knowledge Blocks"
				encodedQuery={encodedQuery}
				outputType='encoded_query'
			/>
		</now-modal>
	);
};

createCustomElement('sn-knowledge-blocks-condition-builder', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {

	},
	properties: {
		encodedQuery: '',
		show: false
	},
	actionHandlers: {
		[NOW_MODAL_CLOSED]: handleCloseModal,
		[ENCODED_QUERY_CHANGED]: handleEncodedQueryChange,
		[ENCODED_QUERY_CHANGED_SUBMITTED]: handleEncodedQueryChangeSubmit
	}
});
