import {
	PREVIEW_RECORD,
	LINK_CLICKED,
	NEXT_BLOCK,
	PREV_BLOCK,
	BLOCK_CONTENT_FETCHED
} from './constants';
import {
	SNIPPET_INSERT_STARTED,
	BLOCK_CONTENT_HANDLE_STARTED
} from '../../constants';

export const handleInsertBlock = (event, dispatch, blockId) => {
	event.stopPropagation();
	dispatch(SNIPPET_INSERT_STARTED, {
		selectedBlockId: blockId
	});
};

export function handleHover(e, state, updateState, dispatch) {
	let newHoverState = state.isHovering;
	if (e.type === 'mouseout') {
		newHoverState = false;
	} else if (e.type === 'mouseover') {
		newHoverState = true;
	} else
		return;
	//only setState if it changed.
	if (newHoverState !== state.isHovering) {
		updateState({isHovering: newHoverState });
	}
}

export function getActiveClass(type, state, updateState) {
	let isHover = state.isHovering;
	let blockId = state.properties.blockDetails.sysId;
	let blockIds = state.properties.blockIds;
	let blockIndex = blockIds.indexOf(blockId);
	let active;
	if (type === NEXT_BLOCK) {
		active = blockIndex < blockIds.length - 1;
		updateState({showNext: active});
	} else if (type === PREV_BLOCK) {
		active = blockIndex > 0;
		updateState({showPrev: active});
	}
	return (active ? '' : 'is-disabled') + ' ' + (isHover ? 'btn-visible' : 'btn-hidden');
}

const handlePreviewBlock = (coeffects) => {
	const {action, dispatch, properties} = coeffects;
	action.stopPropagation();

	let payload = {
		table: 'kb_knowledge_block',
		sys_id: properties.blockDetails.sysId,
		params: {
			sysparm_parent_table: 'kb_knowledge',
			sysparm_parent_sys_id: properties.blockDetails.parentId
		}
	};

	dispatch(PREVIEW_RECORD, payload);
};

const handleBlockContent = (coeffects) => {
	const {action, dispatch, properties} = coeffects;
	let selectedBlockId = properties.blockDetails.sysId;
	let selectedBlockIndex = properties.blockIds.indexOf(selectedBlockId);
	let blockId;
	if (action.payload.type === NEXT_BLOCK) {
		blockId = properties.blockIds[selectedBlockIndex + 1];
	} else if (action.payload.type === PREV_BLOCK) {
		blockId = properties.blockIds[selectedBlockIndex - 1];
	}
	dispatch(BLOCK_CONTENT_HANDLE_STARTED, {selectedBlockId: blockId})
};

export const actions = {
	[LINK_CLICKED]: {
		private: true
	},
	[BLOCK_CONTENT_FETCHED]: {
		private: true
	}
};

export const actionHandlers = {
	[LINK_CLICKED]: handlePreviewBlock,
	[BLOCK_CONTENT_FETCHED]: handleBlockContent
};
