import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import nomatch from '../../icons/no-result-found.svg';
import styles from '../../knowledgeBlocks.scss';
import {NEW_BLOCK_CREATE_STARTED} from '../../constants';

const handleCreateNewBlock = (event, state, dispatch, knowledgeId, knowledgeBase, articleLanguage) => {
	event.stopPropagation();

	let payload = {
		knowledgeId: knowledgeId,
		knowledgeBase: knowledgeBase,
		articleLanguage: articleLanguage
	};

	dispatch(NEW_BLOCK_CREATE_STARTED, payload);
};

const view = (state, {dispatch}) => {
	const {
		properties: {
			knowledgeId,
			knowledgeBase,
			articleLanguage
		}
	} = state;

	return (
		<div className="block-empty-state">
			<div className="block-empty-state-row">
				<img src={nomatch} alt=""/>
			</div>
			<h3 className="block-empty-state-row-header">{ t('No Results Found') }</h3>
			<div className="block-empty-state-row">
				<span>{ t('Refine your search.') }</span>
			</div>
			<now-button label="Create Block" size='md' variant='primary' className='block-button-control'
						on-click={(e) => handleCreateNewBlock(e, state, dispatch, knowledgeId, knowledgeBase, articleLanguage)}/>
		</div>
	);
};

createCustomElement('sn-knowledge-blocks-no-results',{
	renderer : {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		knowledgeId: '',
		knowledgeBase: '',
		articleLanguage: ''
	}
});
