import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from '../../knowledgeBlocks.scss';
import '../sn-blocks-time-ago';
import '@servicenow/now-button';
import '@servicenow/now-text-link';
import {
	BLOCK_CONTENT_HANDLE_STARTED,
	SNIPPET_INSERT_STARTED
} from '../../constants';

const PREVIEW_RECORD = 'PREVIEW_RECORD';

const handlePreviewBlock = (event, state, dispatch, blockId, parentId) => {
	event.stopPropagation();

	let payload = {
		table: 'kb_knowledge_block',
		sys_id: blockId,
		params: {
			sysparm_parent_table: 'kb_knowledge',
			sysparm_parent_sys_id: parentId
		}
	};

	dispatch(PREVIEW_RECORD, payload);
};

const handleBlockContent = (event, dispatch, blockId) => {
	event.stopPropagation();
	if(event.type == 'click' || (event.type =='keydown' && event.keyCode == '13')) {
		if (event.target.tagName !== 'NOW-TEXT-LINK' && event.target.tagName !== 'NOW-BUTTON') {
			dispatch(BLOCK_CONTENT_HANDLE_STARTED, {
				selectedBlockId: blockId
			});
		}
	}
};

const handleInsertSnippet = (event, dispatch, blockId) => {
	event.stopPropagation();
	dispatch(SNIPPET_INSERT_STARTED, {
		selectedBlockId: blockId
	});
};

const view = (state, {dispatch}) => {
	const {
		properties: {
			sysId,
			number,
			description,
			updatedOn,
			blockVersion,
			canRead,
			cannotRead,
			parentSysId
		}
	} = state;

	const canReadElement = canRead ?
		<div className='block-item'>
			<label>{t('Can Read:')}</label>
			{canRead}
		</div> : null;

	const cannotReadElement = cannotRead ?
		<div className='block-item'>
			<label>{t('Cannot Read:')}</label>
			{cannotRead}
		</div> : null;

return (
		<div className='block-item-card' on-click={(e) => handleBlockContent(e, dispatch, sysId)} on-keydown={(e) => handleBlockContent(e, dispatch, sysId)} tabindex="0">
			<div className='block-item-section'>
				<div className='block-item-header'>
					<div className='block-item'>{number}<span className="block-header block-separator">.</span>v{blockVersion}</div>
					<div className='block-item-header-date'>
						{t('Updated')} <sn-blocks-time-ago time_stamp={updatedOn} system_time={true} />
					</div>
				</div>
				<div className='block-item-title kb-ellipsis'>{description}</div>
				{ canReadElement }
				{ cannotReadElement }
			</div>
			<div className='block-button-container'>
				<now-button
					label={t('Insert')}
					aria-label={t(`Insert Block into the Article. Block Description: ${description}`)}
					autoFocus size="sm"
					on-click={(e) => handleInsertSnippet(e, dispatch, sysId)}
				/>
			</div>
		</div>
	);
};

createCustomElement('sn-knowledge-blocks-item', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		sysId: '',
		number: '',
		description: '',
		updatedOn: '',
		blockVersion: '',
		canRead: '',
		cannotRead: '',
		parentSysId: '',
		handleInsertBlock: ''
	}
});
